/* Less than 576px */
.nopm {
    padding: 0px;
    margin: 0px;
}

#logotitle {
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 100px;
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) { 
    #logotitle {
        padding-right: 40vw;
        padding-top: 100px;
        color: white;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

